<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>

    <!-- Navbar start -->
    <Navbar @changeLanguage="setLang"/>
    <!-- Navbar end -->

    <!-- banner start -->
    <div class="container-fuild">
      <div class="index-banner bg-setting banner-img-filter">
        <h3 class="index-banner-title">ATSC</h3>
      </div>
    </div>
    <!-- banner end -->

    <!-- about start -->
    <section class="section">
      <div class="container">
        <div class="row">
          <div class="col-12 col-lg-10 offset-lg-1 col-xl-8 offset-xl-2">
            <div class="row align-items-center">
              <div class="col-md-6">
                <div class="about-img bg-setting"></div>
              </div>
              <div class="col-md-6">
                <div class="about-info">
                  <h3 class="section-title">{{ $t('AboutTitle')}}</h3>
                  <p class="about-text">{{ $t('AboutDesc')}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!-- about end -->

    <!-- product start -->
    <section class="section product-bg-color">
      <div class="container">
        <h3 class="section-title text-center">{{ $t('ProductTitle')}}</h3>
        <div class="row">
          <div class="col-12 col-lg-10 offset-lg-1 col-xl-8 offset-xl-2">
            <div class="row">
              <div class="col-md-4">
                <div class="product-item bg-setting automation product-img-filter">
                  <router-link to="/product/automation" class="more">{{ $t('ProductMore')}}</router-link>
                </div>
              </div>
              <div class="col-md-4">
                <div class="product-item bg-setting hydropower product-img-filter">
                  <router-link to="/product/hydropower" class="more">{{ $t('ProductMore')}}</router-link>
                </div>
              </div>
              <div class="col-md-4">
                <div class="product-item bg-setting vibrationTransport product-img-filter">
                  <router-link to="/product/vibrationTransport" class="more">{{ $t('ProductMore')}}</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!-- product end -->

    <!-- Footer start -->
    <Footer :isChangeLanguge = "changeLanguge"/>
    <!-- Footer end -->
  </div>
</template>
<script>
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import { Circle4 } from 'vue-loading-spinner'

export default {
  name: 'Index',
  components:{
    Navbar,
    Footer,
    Circle4
  },
  data(){
    return{
      isLoading: false,
      changeLanguge: true,
    }
  },
  methods: {
    // 儲存切換的語系
    setLang (value) {
      this.$store.commit('setLang', value);
      this.$i18n.locale = value;
      localStorage.setItem('footmark-lang', value);
      this.changeLanguge = !this.changeLanguge
    }
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.index-banner{
  width: 100%;
  height: 70vh;
  background-image: url('../assets//images/indexBanner.jpg');
  @include screen($mobile){
    height: 70vh;
  }
}

.index-banner-title{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 100px;
  line-height: 1;
  color: white;
  &::before,
  &::after{
    content: '';
    display: block;
    border: 3px solid white;
  }
  &::before{
    margin-bottom: 20px;
  }
  &::after{
    margin-top: 20px;
  }
  @include screen($mobile){
    font-size: 80px;
  }
}

.about-img{
  width: 100%;
  height: 0;
  padding-top: calc(100% / 6 * 4);
  background-image: url('../assets/images/indexAbout.jpg');
  @include screen($mobile){
    margin-bottom: 30px;
  }
}
.about-text{
  margin: 0;
  padding: 0;
  text-align: left;
  font-size: 1rem;
  line-height: 1.5;
}
.product-bg-color{
  background-color: #F7F7F7;
}
.product-item{
  width: 100%;
  height: 300px;
  background-color: #fff;
  box-shadow: 0 10px 20px -10px rgba(0,0,0,0.5);
  @include screen($mobile){
    margin-bottom: 30px;
  }
  &.automation{
    background-image: url('../assets/images/product-1.jpg');
  }
  &.hydropower{
    background-image: url('../assets/images/product-2.jpg');
  }
  &.vibrationTransport{
    background-image: url('../assets/images/product-3.jpg');
  }
}
.more{
  padding: 10px;
  width: 100%;
  background-color: rgba(255,252,255,.75);
  color: #737373;
  font-size: 1.125rem;
  font-weight: bold;
  text-align: center;
  line-height: 1;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: all .3s;
  @include screen($mobile){
    padding: 16px;
    width: 100%;
    color: #14213D;
    font-weight: bold;
    background-color: rgba(255,252,255,1);
  }
  &:hover{
    color: #14213D;
    background-color: rgba(255,252,255,1);
    text-decoration: none;
  }
}
</style>